import { ExpreeImageFilterTool, IImageEditorParam } from './ExpreeImageFilterTool';
export class Demo520ImageFilterTool {
    expreeImageTool: ExpreeImageFilterTool;
    static Init(id: string) {
        const parma: IImageEditorParam = {
            id: id,
            // ui: true,
            colorCover: '#ff0000',
        };
        const n = new Demo520ImageFilterTool(parma);
        n.Dummy();
    }
    constructor(parma: IImageEditorParam) {
        this.expreeImageTool = new ExpreeImageFilterTool(parma);
    }
    public Dummy() {
        // TODO
    }
}

export class Solution {
    public divHost: HTMLElement;

    constructor(id: string) {
        this.divHost = document.getElementById(id);

        Demo520ImageFilterTool.Init(id)
    }
    public destroy() {
        if (this.divHost) {
            try {
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}